.up-button {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 2px solid $color2;
    position: fixed;
    right: 5%;
    bottom: 10%;
    background: $background1;
    cursor: pointer;
    display: none;
    transition: .3s;
    z-index: 1000;

    @include media-breakpoint-down(xs) {
        width: 45px;
        height: 45px;
    }

    &:hover {
        background: $color3;
    }

    &_show {
        transition: .3s;
        display: inline-block;
    }

    span {
        color: $color2;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%);
        font-size: 30px;

        @include media-breakpoint-down(xs) {
            font-size: 25px;
        }
    }
}

body {
    font-family: $font1;
