}

.line-decoration {

    position: relative;

    span {
        &::before {
            content: '';
            height: 2px;
            background: $color2;
            box-shadow: 0 0 10px 5px $color2, inset 0 0 10px 5px $color2;
            position: absolute;
            bottom: -19px;
            left: 0;
            right: -10px;
        }
    }

    &_left {
        margin-bottom: 175px;

        &::before {
            left: 0;
            -webkit-transform: translate(-85px, 90px);
            transform: translate(-85px, 90px);
        }

        &::after {
            left: 0;
            -webkit-transform: translate(-100%, 0) rotate(-45deg);
            transform: translate(-100%, 0) rotate(-45deg);
        }
    }

    &_right {
        margin-bottom: 135px;

        &::before {
            right: 0;
            -webkit-transform: translate(85px, 90px);
            transform: translate(97px, 90px);
        }

        &::after {
            right: -109px;
            -webkit-transform: translate(-100%, 0) rotate(222deg);
            transform: translate(-100%, 0) rotate(222deg);
        }
    }

    &::before {
        content: '';
        background: $color2;
        box-shadow: 0 0 10px 5px $color2, inset 0 0 10px 5px $color2;
        position: absolute;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        bottom: -10px;
    }

    &::after {
        content: '';
        background: $color2;
        box-shadow: 0 0 10px 5px $color2, inset 0 0 10px 5px $color2;
        position: absolute;
        height: 2px;
        bottom: -19px;
        width: 98.98px;
        -webkit-transform-origin: 100% 50%;
        transform-origin: 100% 50%;

    }
}

