.btn {
    display: inline-block;
    color: $background1;
    background-color: $color2;
    font-family: "Montserrat", sans-serif;
    text-decoration: none;
    padding: 12px 20px;
    text-align: center;
    margin-top: 50px;
    min-height: auto;
    font-weight: bold;
    white-space: normal;
    position: relative;
    letter-spacing: 2px;
    transform: translate(-4px, -4px);
    -webkit-transition: -webkit-transform 0.3s ease-in;
    //z-index: 10;
    font-size: 16px;
    width: 250px;


    &::before {
        background: none;
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        border: 2px solid $color;
        width: calc(100% - 3px);
        height: calc(100% - 3px);
        -webkit-transform: translate(7px, 7px);
        transform: translate(7px, 7px);
        -webkit-transition: -webkit-transform 0.3s ease-in;
        transition: -webkit-transform 0.3s ease-in;
        z-index: -2;
    }

    &::after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: $color2;
        z-index: -1;
    }

    &:hover {
        -webkit-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
        -webkit-transition: -webkit-transform 0.3s ease-in;
        transition: -webkit-transform 0.3s ease-in;

        &::before {
            -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
            -webkit-transition: -webkit-transform 0.3s ease-in;
            transition: -webkit-transform 0.3s ease-in;
        }
    }
}

@media (max-width: 1320px) {
    .btn {
        font-size: 15px;
    }
}

a.arrow-down {
