    .footer {
        background: $background1;

        &__txt {
            text-align: center;
            font-family: $font1;
            font-size: 16px;
            font-weight: 600;
            color: $color;
            padding: 40px 0 30px 0;

            @include media-breakpoint-down(xs) {
                font-size: 13px;
                padding: 25px 0 20px 0;

            }
        }

        &__heart {
            background-color: #dd2e44;
            height: 8px;
            width: 8px;
            position: relative;
            transform: rotate(-45deg);
            animation: heartbeat 1s infinite;
            display: inline-block;
            margin: 0 1.5rem;

            @include media-breakpoint-down(xs) {
                margin: 0 0.5rem;

            }

            &::before,
            &::after {
                content: "";
                position: absolute;
                background: #dd2e44;

                border-radius: 100%;
            }

            &::before {
                top: -5px;
                left: 0;
                width: 8px;
                height: 8px;
            }

            &::after {
                top: 0;
                right: -5px;
                width: 8px;
                height: 8px;
            }
        }
    }

    @keyframes heartbeat {
        0% {
            transform: scale(1) rotate(-45deg)
        }

        20% {
            transform: scale(1.25) rotate(-45deg)
        }

        40% {
            transform: scale(1.5) rotate(-45deg)
        }
    }

    ;

    @keyframes float {
        0% {
            transform: translateY(0)
        }

        65% {
            transform: translateY(2.5rem)
        }

        to {
            transform: translateY(0)
        }
    }

    ;